export interface KeepAliveOptions {
  /**
   * milliseconds
   *
   * @default 1000
   */
  minDelay?: number
  /**
   * milliseconds
   *
   * @default Infinity
   */
  maxDelay?: number
  /**
   * Reset attempts number delay (milliseconds).
   *
   * @default 30000
   */
  resetAfter?: number
  /**
   * Abort signal.
   */
  signal?: AbortSignal
  /**
   * Exit handler.
   */
  onExit?: (err: any) => any
}

/**
 * Keep alive an async function with exponential retry.
 */

export function keepAlive(
  fn: () => Promise<any>,
  options: KeepAliveOptions = {}
) {
  const minDelay = options.minDelay ?? 1000
  const maxDelay = options.maxDelay ?? Number.POSITIVE_INFINITY
  const resetAfter = options.resetAfter ?? 30000

  let tmDelay: any
  let tmReset: any

  let aborted = false
  let attempts = 0

  const stop = () => {
    if (tmDelay) {
      clearTimeout(tmDelay)
    }
    if (tmReset) {
      clearTimeout(tmReset)
    }
  }

  const handler = (err: any) => {
    if (aborted) {
      return
    }
    stop()
    if (options.onExit) {
      options.onExit(err)
    }

    tmDelay = setTimeout(() => {
      fn()
        .then(() => handler(null))
        .catch(handler)
    }, Math.min(minDelay * Math.floor(Math.exp(attempts++)), maxDelay))

    tmReset = setTimeout(() => {
      attempts = 0
    }, resetAfter)
  }

  fn()
    .then(() => handler(null))
    .catch(handler)

  function abort() {
    aborted = true
    stop()
  }
  options.signal?.addEventListener('abort', abort)
  return abort
}
